import React, { Component } from 'react';

export default class Message extends Component {
  msgRender = (item, index) => {
    let msgDom = null;
    // 系统消息
    if (item.type === 'system') {
      msgDom = (
        <div className="system_box" key={index}>
          <span className="red">{item.name}：</span>
          {item.text}
          <style jsx>{`
          .system_box {
            margin-bottom: 11px;
            .red {
              color: #FF1E3E;
            }
          }
          `}</style>
        </div>
      );
    }

    // 提示消息
    if (item.type === 'tips') {
      msgDom = (
        <div className="tips_box" key={index}>
          {item.text}
          <style jsx>{`
          .tips_box {
            margin-bottom: 11px;
            color: #16B538;
          }`}
          </style></div>
      );
    }
    // 警告消息
    if (item.type === 'warn') {
      msgDom = (
        <div className="warn_box" key={index}>
          {item.text}
          <style jsx>{`
          .warn_box {
            margin-bottom: 11px;
            color: #FF1E3E;
          }`}
          </style>
        </div>
      );
    }

    // 用户弹幕
    if (item.type === 'barrage') {
      msgDom = (
        <div className="barrage_box" key={index}>
          <p className="nickname">{item.nickname}</p>
          <p className="text">说：{item.text}</p>
          <style jsx>{`
            .barrage_box {
              display: flex;
              justify-content:flex-start;
              align-items: center;
              margin-bottom: 11px;
              .nickname {
                color: #2C8CFB;
              }
              .text {
                color: #353535;
              }
            }
            `}</style>
        </div>
      );
    }

    // 用户送礼
    if (item.type === 'gift') {
      msgDom = (
        <div className="gift_box" key={index}>
          <span className="nickname">{item.nickname}</span>送：
          <img src={item.gift} alt="" className="gift" />
          <span className="num">X{item.num}个</span>
          <style jsx>{`
            .gift_box {
              display: flex;
              justify-content: flex-start;
              align-items: center;
              margin-bottom: 11px;
              .nickname {
                color: #2C8CFB;
              }
              .gift {
                width: 36px;
                height: 36px;
              }
              .num {
                color: #353535;
              }
            }
            `}</style>
        </div>
      );
    }
    return msgDom;
  }

  render() {
    const { data } = this.props;
    console.log(data);
    return (
      <div className="message_box">
        {data.map((item, index) => this.msgRender(item, index))}
        <style jsx>{`
          .message_box {
            width: 340px;
            font-size: 12px;
          }
          `}</style>
      </div>
    );
  }
}
