import React, { Component } from 'react';
import { Tabs, Input } from 'antd';
import PropTypes from 'prop-types';
import Message from './message';
import mock from './message/mock';

import './index.scss';

const { TabPane } = Tabs;

export default class ChatPlate extends Component {
  constructor() {
    super();
    this.state = {
      placeHolder: '不发言怎么撩小姐姐呢~',
    };
  }

  signIn = () => {
    console.log('登录');
  }

  combinePane = (type, list) => {
    const { placeHolder } = this.state;
    const { isSign } = this.props;
    let paneDom = null;
    if (type === 'chat') {
      paneDom = (
        <Message data={mock} />
      );
    }
    if (type === 'management') {
      paneDom = (
        <div className="management_box">
          {list.map((item, index) => (
            <div className="item" key={index}>
              <div className="left">
                <div className="badge"></div>
                <div className="avatar_box">
                  <img src={item.avatar} alt="头像" />
                </div>
                <p className="nickname">{item.nickname}</p>
              </div>
              <div className="right">
                <div className="grade">{item.grade}</div>
              </div>
            </div>
          ))}
        </div>
      );
    }
    return (<div className="pane_box">
      {paneDom}
      <div className="chat_input_box">
        <Input className='chat_input' placeholder={placeHolder} />
        <div className="btn_send">发送</div>
        <div className={`sign_tips ${isSign ? 'hide' : 'show'}`}>
          <span className='red' onClick={this.signIn}>登录</span>后才可以聊天哦！
        </div>
      </div>
    </div>);
  }

  render() {
    const { data } = this.props;
    console.log(data);
    return (
      <div className="chat_plate_box">
        <Tabs defaultActiveKey="1">
          <TabPane tab="聊天" key="1">{this.combinePane('chat')}</TabPane>
          <TabPane tab="管理" key="2">{this.combinePane('management', data.managers)}</TabPane>
        </Tabs>
      </div>
    );
  }
}

const {
  bool, arrayOf, shape, string, number,
} = PropTypes;

ChatPlate.propTypes = {
  isSign: bool,
  data: shape({
    managers: arrayOf(shape({
      nickname: string,
      grade: number,
      avatar: string,
    })),
  }),
};
