import ajax from '@fdaciuk/ajax';
import mockForm from './mockForm';
import config from './config';
import { getEnv, getAuthentication, getAuthenticationToken } from './util';


// 约定先行，配置动态
const ENV = getEnv().NODE_ENV;

let count = 0;

console.log(ENV);
// console.log('userAgent:', navigator.userAgent);


export default async function request(param, plugin) {
  const paramCopy = JSON.parse(JSON.stringify(param));

  const accessToken = ENV === 'development' ? getAuthenticationToken('accessToken') : getAuthenticationToken('accessToken');
  const platform = getAuthentication('platform') ? getAuthentication('platform') : '';
  const packageId = getAuthentication('package') ? getAuthentication('package') : 0;
  // console.log('查看下鉴权信息', accessToken, platform, packageId);
  // console.log(accessToken);
  paramCopy.headers = Object.assign({}, {
    accessToken,
    platform,
    packageId,
    'content-type': 'application/x-www-form-urlencoded',
    // 'content-type': 'application/json',
  }, param.headers);

  paramCopy.url = `${config.apiHost[ENV]}${param.url}`;
  // console.log(`${config.apiHost[ENV]}${param.url}`);
  let tmpData = '';
  if (param.data) {
    Object.keys(param.data).forEach((key) => {
      tmpData += `${key}=${param.data[key]}&`;
    });
    paramCopy.data = tmpData.substring(0, tmpData.length - 1);
  }
  return new Promise((resolve, reject) => {
    let re = null;
    if (plugin && plugin.name === 'mockForm') {
      re = mockForm(paramCopy, plugin.data);
    } else {
      re = ajax(paramCopy);
    }
    re.then(async (res) => {
      // console.log('request res', res);
      if (res.code === 0) {
        resolve(res.data);
      } else if (res.code === 901) {
        // 无效的token
        if (count < 3) {
          request(param, plugin);
        } else {
          console.log('异常登入');
          reject(res);
        }
        count += 1;
        // console.log(count += 1);
      } else {
        // 其它错误弹出 后端返回的 msg
        // console.log('asd', res));
        reject(res); // 其他的处理也放在这里
      }
    }).catch((err, xml) => {
      // if (xml && xml.status === 0) {
      //   reject(err, '错误');
      // }
      console.log('request.js 请求错误需要返回', err, xml);
      reject(err);
    });
  });
}
