import React, { Component } from 'react';
import { Tooltip } from 'antd';
import { connect } from 'react-redux';
import classnames from 'classnames';
import Container from '../container/index';
import Boy from '../../../asset/img/login/boy@2x.png';
import BoyP from '../../../asset/img/login/boy_pre@2x.png';
import Girl from '../../../asset/img/login/girl@2x.png';
import GirlN from '../../../asset/img/login/girl_n@2x.png';
import '../index.scss';
import {
  setRePhone,
  verifyRePhone,
  codeClick,
  setReCode,
  verifyReCode,
  setReName,
  verifyReNmae,
  setRePwd,
  verifyRePwd,
  setReSex,
  registerClick,
} from '../../../view/actions/login/index';


class RegisterConter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      protocol: true,
    };
  }

  componentDidMount = () => {

  }

  handleClick() {
    const { getCode } = this.props;
    const { rephone } = this.props.login;
    getCode(rephone.value);
  }

  render() {
    const {
      settingPhone,
      testPhone,
      settingReCode,
      testReCode,
      settingReName,
      testReName,
      settingRePwd,
      testRePwd,
      settingReSex,
    } = this.props;
    const {
      rephone, recode, rename, repwd, resex, verifyTimer,
    } = this.props.login;
    const { protocol } = this.state;
    return (
      <div className={classnames('content')}>
        <Container>
          <div className={classnames('left')}>
            <div>
              <Tooltip
                placement="right"
                title={rephone.msg}
                visible={rephone.visible}
              >
                <input
                  placeholder="请输入手机号"
                  className={classnames('enter')}
                  onChange={e => settingPhone(e.target.value)}
                  onBlur={e => testPhone(e.target.value)}
                />
              </Tooltip>
            </div>
            <div className={classnames('code')}>
              <Tooltip
                placement="right"
                title={recode.msg}
                visible={recode.visible}
              >
                <input
                  placeholder="请输入验证码"
                  className={classnames('enter')}
                  onChange={e => settingReCode(e.target.value)}
                  onBlur={e => testReCode(e.target.value)}
                />
                <div className={classnames('obtain')} onClick={() => this.handleClick()}>{verifyTimer}</div>
              </Tooltip>
            </div>
            <div>
              <Tooltip
                placement="right"
                title={rename.msg}
                visible={rename.visible}
              >
                <input
                  placeholder="请输入昵称"
                  className={classnames('enter')}
                  onChange={e => settingReName(e.target.value)}
                  onBlur={e => testReName(e.target.value)}
                />
              </Tooltip>
            </div>
            <div>
              <Tooltip
                placement="right"
                title={repwd.msg}
                visible={repwd.visible}
              >
                <input
                  placeholder="请输入密码"
                  className={classnames('enter')}
                  type="password"
                  onChange={e => settingRePwd(e.target.value)}
                  onBlur={e => testRePwd(e.target.value)}
                />
              </Tooltip>
            </div>
            <div className={classnames('sex')}>
              <span>性别</span>
              <p onClick={() => settingReSex(1)} className={classnames({ active: resex === 1 })}>
                男 <img src={(resex === 1 ? BoyP : Boy)} alt="男" />
              </p>
              <p className={classnames({ active: resex === 2 })} onClick={() => settingReSex(2)}>
                女 <img src={(resex === 2 ? Girl : GirlN)} alt="女" />
              </p>
            </div>
            <div className={classnames('verif')}>
              <p>
                <span>
                  <i />
                </span>
                点击开始验证
              </p>
            </div>
            <div className={classnames('live')}>
              <input type="checkbox" checked={protocol} onChange={() => this.handleChange(protocol)}/> &nbsp;我已阅读并允许{' '}
              <span>直播用户协议</span>
            </div>
            <div className={classnames('btn')} onClick={() => this.handleSub()}>注册</div>
          </div>
        </Container>
      </div>
    );
  }

  handleChange = (value) => {
    this.setState({
      protocol: !value,
    });
  }

  handleSub = () => {
    const {
      registering,
    } = this.props;
    const {
      rephone, recode, rename, repwd, resex,
    } = this.props.login;
    registering(rephone.value, recode.value, rename.value, repwd.value, resex, this.state.protocol);
  };
}

export default connect(
  ({ login }) => ({
    login,
  }),
  dispatch => ({
    settingPhone(value) {
      dispatch(setRePhone(value));
    },
    testPhone(value) {
      dispatch(verifyRePhone(value));
    },
    getCode(mobile) {
      dispatch(codeClick(mobile));
    },
    settingReCode(value) {
      dispatch(setReCode(value));
    },
    testReCode(value) {
      dispatch(verifyReCode(value));
    },
    settingReName(value) {
      dispatch(setReName(value));
    },
    testReName(value) {
      dispatch(verifyReNmae(value));
    },
    settingRePwd(value) {
      dispatch(setRePwd(value));
    },
    testRePwd(value) {
      dispatch(verifyRePwd(value));
    },
    settingReSex(value) {
      dispatch(setReSex(value));
    },
    registering(username, code, nickname, password, sex, protocol) {
      dispatch(registerClick(username, code, nickname, password, sex, protocol));
    },
  }),
  //   mapStateToProps,
  //   mapDispatchToProps
)(RegisterConter);
