import React, { Component } from 'react';
import { Tabs } from 'antd';
import PropTypes from 'prop-types';
import './index.scss';

const { TabPane } = Tabs;

export default class ContributionList extends Component {
  listPane = (type, list) => {
    let listDom = null;
    if (type === 'contribution') {
      listDom = list.map((item, index) => (
        <div className="item" key={index}>
          <div className="left">
            {/* 贡献排名 */}
            <div className={`ranking_box rank_${item.ranking}`}>
              <p className="ranking">{item.ranking > 3 ? item.ranking : ''}</p>
            </div>
            <div className="avatar_box">
              <img src={item.avatar} alt="头像" />
            </div>
            <p className="nickname">{item.nickname}</p>
            <div className="grade">{item.grade}</div>
          </div>
          <div className="right">
            {/* 消费 */}
            <div className="consumption">{item.consumption}豪钻</div>
          </div>
        </div>
      ));
    } if (type === 'onlines') {
      listDom = list.map((item, index) => (
        <div className="item" key={index}>
          <div className="left">
            <div className="avatar_container">
              <img src={item.avatar} alt="头像" />
            </div>
            <p className="nickname">{item.nickname}</p>
          </div>
          <div className="right">
            <div className="grade">{item.grade}</div>
          </div>
        </div>
      ));
    }
    return <div className='list_pane'>{listDom}</div>;
  }

  render() {
    const { data, onlinePeople } = this.props;
    return <div className='contribution_list'>
      <Tabs defaultActiveKey="1">
        <TabPane tab="周贡献榜" key="1">{this.listPane('contribution', data.contributions)}</TabPane>
        <TabPane tab={`在线用户(${onlinePeople})`} key="2">{this.listPane('onlines', data.onlines)}</TabPane>
      </Tabs>
    </div>;
  }
}

const {
  number, string, shape, arrayOf,
} = PropTypes;

ContributionList.propTypes = {
  onlinePeople: number,
  data: shape({
    contributions: arrayOf(shape({
      ranking: number,
      avatar: string,
      nickname: string,
      grade: number,
      consumption: number,
    })),
    onlines: arrayOf(shape({
      nickname: string,
      avatar: string,
      grade: number,
    })),
  }),
};
