import request from './request';

export async function testReq() {
  const param = {
    method: 'GET',
    url: '/categories',
  };
  try {
    const res = await request(param);
    return res;
  } catch (err) {
    throw err;
  }
}

// 用户点击登录
export async function UserLogin(url) {
  const param = {
    method: 'POST',
    url: '/auth/mobile-login',
    data: url,
  };
  try {
    const res = await request(param);
    return res;
  } catch (err) {
    throw err;
  }
}

// 点击获取验证码
export async function sendVcode(data) {
  const param = {
    method: 'GET',
    url: '/mobile/send-vcode',
    data,
  };
  const res = await request(param);
  return res;
}

