// 发送请求按钮的锁定和未锁定状态机
/**
 * @class LockMachine
 * @desc 简单状态机，提供 true ，false 两种状态的切换方法。
 */
export default class LockMachine {
  constructor(initState) {
    if (typeof initState === 'boolean') {
      this.curState = initState;
    } else {
      throw new Error('typeError: invalid val');
    }
  }

  log() {
    console.log(this.curState);
  }

  lock() {
    this.curState = true;
  }

  unLock() {
    this.curState = false;
  }
}
