import { MODAL_LOGIN, MODAL_REGISTER, MODAL_CLOSE } from '../../constants/modal/index';

export const modalLogin = () => ({
  type: MODAL_LOGIN,
});

export const modalRegister = () => ({
  type: MODAL_REGISTER,
});

export const modalClose = () => ({
  type: MODAL_CLOSE,
});

export function clickLogin() {
  return (dispatch) => {
    dispatch(modalLogin());
  };
}

export function clickRegister() {
  return (dispatch) => {
    dispatch(modalRegister());
  };
}

export function clickClose() {
  return (dispatch) => {
    dispatch(modalClose());
  };
}
