/**
 * 模拟表单上传多文件(作为 ajax 的插件使用)
 */

export default function mockForm(param, data) {
  return new Promise((resolve, reject) => {
    const XHR = new XMLHttpRequest();
    // 定义成功的数据提交后会发生什么
    XHR.addEventListener('load', (xhr) => {
      const res = JSON.parse(xhr.target.response);
      console.log('mockForm', res.code);
      if (res.code === 0) {
        resolve(res);
      } else {
        // code 不为 0 的，也需要 resolve , 由 request.js 来处理 catch throw
        resolve(res);
      }
    });
    // 定义失败的情况会发生什么
    XHR.addEventListener('error', (err) => {
      reject(err);
    });
    // 配置请求
    XHR.open(param.method, param.url);
    // 添加表单数据 POST 请求所需的HTTP请求头
    XHR.setRequestHeader('accessToken', param.headers.accessToken);
    console.log('传的数据', data);
    // 最后，发送数据
    XHR.send(data);
  });
}
