import {
  LOGIN_PHONE, LOGIN_PWD, REGISTER_PHONE, REGISTER_PWD, REGISTER_CODE, REGISTER_NAME, REGISTER_SEX,
  REGISTER_SEND_CODE,
} from '../../constants/login/index';

const INITIAL_STATE = {
  login: {
    phone: {
      value: '',
      visible: false,
      msg: '',
    },
    pwd: {
      value: '',
      visible: false,
      msg: '',
    },
  },
  rephone: {
    value: '',
    visible: false,
    msg: '',
  },
  repwd: {
    value: '',
    visible: false,
    msg: '',
  },
  recode: {
    value: '',
    visible: false,
    msg: '',
  },
  rename: {
    value: '',
    visible: false,
    msg: '',
  },
  resex: 1,

  verifyTimer: '获取验证码',
};

export default function counter(state = INITIAL_STATE, action) {
  switch (action.type) {
    case LOGIN_PHONE:
      return {
        ...state,
        login: {
          phone: {
            value: action.data.value || state.login.phone.value,
            visible: action.data.visible,
            msg: action.data.msg || state.login.phone.msg,
          },
          pwd: state.login.pwd,
        },
      };
    case LOGIN_PWD:
      return {
        ...state,
        login: {
          phone: state.login.phone,
          pwd: {
            value: action.data.value || state.login.pwd.value,
            visible: action.data.visible,
            msg: action.data.msg || state.login.pwd.msg,
          },
        },
      };
    case REGISTER_PHONE:
      return {
        ...state,
        rephone: {
          value: action.data.value || state.rephone.value,
          visible: action.data.visible,
          msg: action.data.msg || state.rephone.msg,
        },
      };
    case REGISTER_PWD:
      return {
        ...state,
        repwd: {
          value: action.data.value || state.repwd.value,
          visible: action.data.visible,
          msg: action.data.msg || state.repwd.msg,
        },

      };
    case REGISTER_CODE:
      return {
        ...state,
        recode: {
          value: action.data.value || state.recode.value,
          visible: action.data.visible,
          msg: action.data.msg || state.recode.msg,
        },

      };
    case REGISTER_NAME:
      return {
        ...state,
        rename: {
          value: action.data.value || state.rename.value,
          visible: action.data.visible,
          msg: action.data.msg || state.rename.msg,
        },

      };
    case REGISTER_SEX:
      return {
        ...state,
        resex: action.data || state.resex,
      };
    case REGISTER_SEND_CODE: {
      return {
        ...state,
        verifyTimer: action.data || state.verifyTimer,
      };
    }
    default:
      return state;
  }
}
