import React, { Component } from 'react';
import { Tooltip } from 'antd';
import { connect } from 'react-redux';
import classnames from 'classnames';
import Container from '../container/index';
import {
  verifyPhone,
  setPhone,
  setPwd,
  verifyPwd,
  loginClick,
} from '../../../view/actions/login/index';
import '../index.scss';

class LoginConter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      value: [],
    };
  }


  handleTest = () => {
    const { loginSub } = this.props;
    const { phone, pwd } = this.props.login.login;
    const username = phone.value;
    const password = pwd.value;
    loginSub(username, password);
  };

  render() {
    const {
      settingPhone, testPhone, settingPwd, testPwd, login,
    } = this.props;
    const { phone, pwd } = login.login;

    return (
      <div className={classnames('content')}>
        <Container>
          <div className={classnames('left')}>
            <div>
              <Tooltip
                placement="right"
                title={phone.msg}
                visible={phone.visible}
              >
                <input
                  placeholder="用户名/手机"
                  className={classnames('enter')}
                  type="text"
                  onChange={e => settingPhone(e.target.value)}
                  onBlur={e => testPhone(e.target.value)}
                />
              </Tooltip>
            </div>
            <div>
              <Tooltip
                placement="right"
                title={pwd.msg}
                visible={pwd.visible}>
                <input
                  placeholder="密码"
                  className={classnames('enter')}
                  type="password"
                  onChange={e => settingPwd(e.target.value)}
                  onBlur={e => testPwd(e.target.value)}
                />
              </Tooltip>
            </div>

            <div className={classnames('text')}>
              <div>
                <input type="checkbox" defaultChecked={true} value="yes"/> &nbsp;7天内自动登录
              </div>
              <div>忘记密码</div>
            </div>
            <div
              className={classnames('btn')}
              onClick={() => this.handleTest()}
            >
              登录
            </div>
            <div> {this.state.props1}</div>
          </div>
        </Container>
      </div>
    );
  }
}

export default connect(
  ({ login }) => ({
    login,
  }),
  dispatch => ({
    settingPhone(value) {
      dispatch(setPhone(value));
    },
    testPhone(value) {
      dispatch(verifyPhone(value));
    },
    settingPwd(value) {
      dispatch(setPwd(value));
    },
    testPwd(value) {
      dispatch(verifyPwd(value));
    },
    loginSub(...value) {
      dispatch(loginClick(...value));
    },
  }),
  //   mapStateToProps,
  //   mapDispatchToProps
)(LoginConter);
