import React from 'react';
import { createStore, compose, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import {
  BrowserRouter, Route, Switch,
} from 'react-router-dom';
import thunk from 'redux-thunk';
import Loadable from 'react-loadable';
import reducers from './reducers';
import MyLoadingComponent from './page/loading/loading';
import Header from '../components/header/index';
import Footer from '../components/footer';
import Login from '../components/login/index';
import Download from './page/download';
import Room from './page/room';

// 路由按需加载
const Index = Loadable({
  loader: () => import('./page/index/index'),
  loading: MyLoadingComponent,
  // 多少时间后显示 MyLoadingComponent 组件
  delay: 300,
});

const Ranking = Loadable({
  loader: () => import('./page/ranking/index'),
  loading: MyLoadingComponent,
  // 多少时间后显示 MyLoadingComponent 组件
  delay: 300,
});
const Search = Loadable({
  loader: () => import('./page/seach/index'),
  loading: MyLoadingComponent,
  // 多少时间后显示 MyLoadingComponent 组件
  delay: 300,
});

const store = createStore(
  reducers,
  compose(
    applyMiddleware(thunk),
    // NOTE:代码里不应该强制依赖浏览器插件，至少要有个自动开关
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__(),
  ),
);
const RouterIndex = () => (
  <Provider store={store}>
    <BrowserRouter>
      <div>
        <Header></Header>
        <Switch>
          <Route path="/" component={Index} exact />
          <Route path="/ranking" component={Ranking} exact />
          <Route path="/search" component={Search} exact />
          <Route path="/download" component={Download} exact/>
          <Route path="/room" component={Room} exact />
        </Switch>
        <Login/>
      </div>
    </BrowserRouter>
    <Footer />
  </Provider>
);

export default RouterIndex;
