import _ from 'lodash';
import JSEncrypt from 'jsencrypt';
import { message } from 'antd';
import {
  LOGIN_PHONE,
  LOGIN_PWD,
  REGISTER_PHONE,
  REGISTER_PWD,
  REGISTER_CODE,
  REGISTER_NAME,
  REGISTER_SEX,
  REGISTER_SEND_CODE,
} from '../../constants/login/index';
import { verify } from '../../../asset/js/util';
import { UserLogin, mobileReg, sendVcode } from '../../../asset/js/api';
import publicKey from '../../../asset/js/publicKey';
import LockMachine from '../../../asset/js/LockMachine';
import HourGlass from '../../../asset/js/HourGlass';
import { clickClose } from '../modal/index';
import { getInfo } from '../user/index';
// 获取验证码按钮锁定
const verifyLock = new LockMachine(false);

const hourGlass = new HourGlass(60);

// 处理下待加密信息里的 ‘+’ 号
function encode(data) {
  return encodeURI(data).replace(/\+/g, '%2B');
  // return btoa(data);
}

export const loginPhone = data => ({
  type: LOGIN_PHONE,
  data,
});

export const loginPwd = data => ({
  type: LOGIN_PWD,
  data,
});
export const registerPhone = data => ({
  type: REGISTER_PHONE,
  data,
});

export const registerPwd = data => ({
  type: REGISTER_PWD,
  data,
});
export const registerCode = data => ({
  type: REGISTER_CODE,
  data,
});
export const registerName = data => ({
  type: REGISTER_NAME,
  data,
});
export const registerSex = data => ({
  type: REGISTER_SEX,
  data,
});
export const registerGetCode = data => ({
  type: REGISTER_SEND_CODE,
  data,
});

// 获取手机号
export function setPhone(value) {
  const phone = {
    value,
  };
  return (dispatch) => {
    dispatch(loginPhone(phone));
  };
}
//  校验手机号
export function verifyPhone(value) {
  let phone = {};
  if (_.isEmpty(value)) {
    phone = {
      visible: true,
      msg: '手机号码不能为空',
    };
    return (dispatch) => {
      dispatch(loginPhone(phone));
      setTimeout(() => {
        phone = {
          visible: false,
        };
        dispatch(loginPhone(phone));
      }, 2000);
      return false;
    };
  }
  if (!verify('phoneNum', value)) {
    phone = {
      visible: true,
      msg: '请输入正确的手机号码',
    };
    return (dispatch) => {
      dispatch(loginPhone(phone));
      setTimeout(() => {
        phone = {
          visible: false,
        };
        dispatch(loginPhone(phone));
      }, 2000);
      return false;
    };
  }
  phone = {
    value,
    visible: false,
  };
  return (dispatch) => {
    dispatch(loginPhone(phone));
    return true;
  };
}
