// 点击获取验证码
export function codeClick(mobile) {
  return (dispatch) => {
    if (!verifyLock.curState && dispatch(verifyRePhone(mobile))) {
      const url = {
        mobile,
      };
      verifyLock.lock();
      hourGlass.init({
        run: (current) => {
          dispatch(registerGetCode(`${current}s`));
        },
        end: () => {
          dispatch(registerGetCode('获取验证码'));
          verifyLock.unLock();
          hourGlass.restart();
        },
      });
      hourGlass.start();

      sendVcode(url)
        .then((data) => {
          console.log(data);
        })
        .catch((err) => {
          message.error(err.msg);
        });
    }
  };
}

