// 用户注册
export function registerClick(username, code, nickname, password, sex, protocol) {
  return (dispatch) => {
    if (
      dispatch(verifyRePhone(username))
      && dispatch(verifyReCode(code))
      && dispatch(verifyReNmae(nickname))
      && dispatch(verifyRePwd(password))
    ) {
      if (protocol) {
        const encrypt = new JSEncrypt(); // 全局变量中
        encrypt.setPublicKey(publicKey);

        const url = {
          nickname,
          sex,
          password: encode(encrypt.encrypt(password)),
        };
        mobileReg(url)
          .then(() => {
            message.success('注册成功');
            dispatch(clickClose());
            dispatch(getInfo());
          })
          .catch((err) => {
            message.error(err.msg);
          });
      } else {
        message.error('请勾选用户信息');
      }
    }
  };
}
