import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Card } from 'antd';
import './index.scss';

export default class AnchorShowCard extends Component {
  render() {
    const {
      data, handleClick, styles, imgSize,
    } = this.props;
    let tipsText = null;
    let btnPlay = null;
    if (data.isPlaying === 0) {
      tipsText = <div className="tips">休息中</div>;
    } else if (data.isPlaying === 1) {
      btnPlay = <div className="cover"><div className="btn_play" /></div>;
    }
    const style = styles || { width: 194 };
    const imgsize = imgSize || { height: '194px' };
    console.log(data);
    return (
      <div className="anchor_show_card_container" onClick={handleClick}>
        <Card
          hoverable
          style={style}
          size="small"
          cover={
            <div style={imgsize}>
               <img alt="show_picture" className='anchor_cover' src={data.avatar} />
            </div>
          }
          bordered={false}
        >
          {tipsText}
          {btnPlay}
          <div className="content">
            <div className="head">
              <p className="title">{data.nickname}</p>
              <p className="nums">{data.onlineNum}人</p>
            </div>
            <p className="desc">{data.announcement}</p>
          </div>
        </Card>
      </div>
    );
  }
}

const {
  string, number, func, shape, object,
} = PropTypes;

AnchorShowCard.propTypes = {
  data: shape({
    avatar: string,
    nickname: string,
    onlineNum: number,
    isPlaying: number, // playing, rest
    announcement: string,
  }),
  handleClick: func,
  styles: object,
  imgSize: object,
};
