import { USER_INFO, USER_LOGIN_OUT } from '../../constants/user/index';

const INITIAL_STATE = {
  status: false,
  info: {},
};

export default function counter(state = INITIAL_STATE, action) {
  switch (action.type) {
    case USER_INFO:
      return {
        ...state,
        info: action.data,
        status: action.status,
      };
    case USER_LOGIN_OUT:
      return {
        ...state,
        info: action.data,
        status: action.status,
      };
    default:
      return state;
  }
}
