import React, { Component } from 'react';
import PropTypes from 'prop-types';
import AnchorShowCard from '../anchor_show_card';
import './index.scss';

export default class AnchorList extends Component {
  cardClick = (index) => {
    console.log('组件顺序', index);
  }

  render() {
    const { data, header } = this.props;
    const anchorList = data.map((anchor, index) => <div className="anchor" key={index}><AnchorShowCard data={anchor} handleClick={this.cardClick.bind(this, index)} /></div>);

    const headerBadgeClass = `badge ${header.badge}`;

    const tips = header.badge === 'recommend' ? <p className='tips'>已加载完毕~</p> : null;

    return (
      <div className='anchor_list_container'>
        <div className="header">
          <div className={headerBadgeClass}></div>
          <p>{header.title}</p>
        </div>
        <div className="list_container">
          {anchorList}
        </div>
        {tips}
      </div>
    );
  }
}
const {
  string, number, shape, arrayOf,
} = PropTypes;
AnchorList.propTypes = {
  data: arrayOf(shape({
    img: string,
    nickname: string,
    onlineNum: number,
    announcement: string,
    isPlaying: number, // playing, rest
  })),
  header: shape({
    badge: string,
    title: string,
  }).isRequired,
};
