import { MODAL_LOGIN, MODAL_REGISTER, MODAL_CLOSE } from '../../constants/modal/index';

const INITIAL_STATE = {
  status: MODAL_CLOSE,
};

export default function counter(state = INITIAL_STATE, action) {
  switch (action.type) {
    case MODAL_LOGIN:
      return {
        ...state,
        status: action.type,
      };
    case MODAL_REGISTER:
      return {
        ...state,
        status: action.type,
      };
    case MODAL_CLOSE:
      return {
        ...state,
        status: action.type,
      };
    default:
      return state;
  }
}
