/**
 * 沙漏，倒数，秒
 */

export default class HourGlass {
  constructor(times) {
    this.times = times;
    this.current = times;
  }

  // 初始化函数
  init(obj) {
    this.run = obj.run;
    this.end = obj.end;
  }

  // 启动沙漏
  start() {
    this.timeTag = setInterval(() => {
      // console.log('启动', this.current);
      if (this.current > 0) {
        this.run(this.current);
        this.current -= 1;
      } else {
        this.end(this.current);
        this.stop();
      }
    }, 1000);
  }

  // 重启沙漏
  restart() {
    this.stop();
    this.current = this.times;
    this.start(this.run, this.end);
  }

  // 停止沙漏
  stop() {
    clearInterval(this.timeTag);
  }

  // 暂停
  pause() {
    // 暂存时间
    this.pauseTime = this.current;
    this.stop();
  }

  // 继续
  continue() {
    this.current = this.pauseTime;
    this.start();
  }
}
