import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Tabs, Tooltip } from 'antd';
import classnames from 'classnames';
import LoginConter from './login_center/index';
import RegisterConter from './register_center/index';
import {
  clickClose,
} from '../../view/actions/modal/index';
import { MODAL_LOGIN, MODAL_REGISTER, MODAL_CLOSE } from '../../view/constants/modal/index';
import './index.scss';

const { TabPane } = Tabs;
const text = <span>擦擦擦擦</span>;
class Login extends Component {
  constructor(props) {
    super(props);
    let key = '';
    const { modal } = props;
    if (modal.status === MODAL_LOGIN) {
      key = '1';
    } else if (modal.status === MODAL_REGISTER) {
      key = '2';
    } else {
      key = '1';
    }
    this.state = {
      key,
      visible: false,
    };
  }

  tableChange = (key) => {
    this.setState({
      key,
    });
  }

  render() {
    const { key } = this.state;
    const { modal, closeModal } = this.props;
    return (
      <div
        className={classnames({
          'mask-modal-login': key === '1',
          'mask-modal-register': key === '2',
        })}
        style={{ display: (modal.status === MODAL_CLOSE ? 'none' : 'flex') }}
      >
        <div className={classnames('modal')}>
          <Tabs
            tabBarExtraContent={<span className={classnames('tit-close')} onClick={() => closeModal()}>X</span>}
            type={'card'}
            activeKey={this.state.key}
            onChange={activeKey => this.tableChange(activeKey)}
          >
            <TabPane tab="登录" key="1">
              <LoginConter />
            </TabPane>
            <TabPane tab="注册" key="2">
              <RegisterConter />
            </TabPane>
          </Tabs>
        </div>
        <div className={classnames('bind-phone')} style={{ display: 'none' }}>
          <p className={classnames('btn-close')}>
            <span />
          </p>
          <div className={classnames('cont')}>
            <h5>绑定手机号</h5>
            <div className={classnames('inp-box')}>
              <div>
                <Tooltip
                  placement="right"
                  title={text}
                  visible={this.state.visible}
                >
                  <input
                    placeholder="请输入手机号"
                    className={classnames('enter')}
                  />
                </Tooltip>
              </div>
              <div className={classnames('code')}>
                <input
                  placeholder="请输入验证码"
                  className={classnames('enter')}
                />
                <div className={classnames('obtain')}>获取验证码</div>
              </div>
            </div>
          </div>
          <div className={classnames('btn')}>提交</div>
        </div>
      </div>
    );
  }

  componentWillReceiveProps = (nextProps) => {
    if (this.props.modal.status !== nextProps.modal.status) {
      let key = '';
      const { modal } = nextProps;
      if (modal.status === MODAL_LOGIN) {
        key = '1';
      } else if (modal.status === MODAL_REGISTER) {
        key = '2';
      } else {
        key = '1';
      }
      this.setState({
        key,
      });
    }
  }
}

export default connect(
  ({ modal }) => ({
    modal,
  }),
  dispatch => ({
    closeModal() {
      dispatch(clickClose());
    },
  }),
)(Login);
