import React, { Component } from 'react';
import {
  Link,
} from 'react-router-dom';
import {
  Input, Icon, Avatar, Badge,
} from 'antd';
import './index.scss';
import { Line } from 'rc-progress';
import { connect } from 'react-redux';
import logo from '../../asset/img/header/logo@2x.png';
import living from '../../asset/img/header/btn_begin_show_nor@2x.png';
import download from '../../asset/img/header/btn_download_nor@2x.png';
import users from '../../asset/img/header/btn_my_nor@2x.png';
import women from '../../asset/img/header/female@2x.png';
import men from '../../asset/img/header/icon_boy@3x.png';
import { getInfo, userLoginOut } from '../../view/actions/user/index';
import { clickLogin, clickRegister } from '../../view/actions/modal/index';

const { Search } = Input;
class Header extends Component {
  state = {
    userInfo: {
      nickname: '大白大白',
      coin: 99999,
      sex: 1,
      level: 1,
      nextLevel: 2,
    },
    angle: 0,
  };

  componentDidMount = () => {
    const { userInit } = this.props;
    userInit();
  }

  render() {
    const { angle } = this.state;
    const {
      user, usetOut, loginModal, registerModal,
    } = this.props;
    const { info } = user;
    return (
    <div className='header_wrap'>
      <div className='container'>
        {/* logo */}
        <img src={logo} alt="logo" className='logo' />

        <div className='navbar'>
          <ul>
            <li className={window.location.pathname === '/' ? 'active' : ''}><Link to='/'>首页</Link></li>
            <li className={window.location.pathname === '/ranking' ? 'active' : ''}><Link to='/ranking'>排行榜</Link></li>
            <li className={window.location.pathname === '/income' ? 'active' : ''}><Link to='/'>充值</Link></li>
          </ul>
        </div>

        <div className='search_input'>
          <Search
            placeholder="请输入主播ID"
            onSearch={value => console.log(value)}
            style={{ width: 200 }}
            className='search'
          />
        </div>

        <div className='action_btn'>
          <img src={living} alt="living_img" />
          <img src={download} alt="download_img" />
        </div>

        <div className='user'>
          {user.status ? (
            <div className='user_info'>
              <Badge dot>
                <Avatar size={32} src={info.avatar} />
              </Badge>
              <span className='user_name'>{info.nickname}</span>&nbsp;
              <Icon style={{ fontSize: 12 }} type="down" rotate={angle} />

              <div className='user_card'>
                <div className='user_info'>
                  <div className='quit' onClick={ () => usetOut()}>
                    <Icon type="upload" rotate={90} />退出
                  </div>
                  <Avatar size={76} src={info.avatar} />
                  <p>{info.nickname}</p>
                  <p>{info.signature}<Icon style={{ fontSize: 12 }} type="form" /></p>
                  <p className='grade_img'>
                  {
                    info.sex === 1 ? <img src={men} className='sex' alt="sex" /> : ''
                  }
                  {
                    info.sex === 2 ? <img src={women} className='sex' alt="sex" /> : ''
                  }

                    <img className='grade' src="http://live-static.utoooo.com/common/level/icon_user_grade_1.png" alt="grade" />
                    <img className='grade' src="http://live-static.utoooo.com/common/level/icon_anchor_grade_1.png" alt="grade" />
                  </p>
                  <div className='progress'>
                    <span className='from'>LV{info.level}</span>
                    <div className='progress'>
                      <Line percent="20" strokeWidth={5} trailWidth={5} strokeColor="#EF0716" trailColor="#fff" style={{ width: 210 }} className='progress_grade' ></Line>
                      <span className='progress_percent'>100/200</span>
                    </div>
                    <span className='to'>LV{Number(info.level, 2) + 1}</span>
                  </div>
                </div>
                <div className='user_wallet'>
                    <span>我的钱包</span>
                    <span className='wallet_money'>{info.coin}</span>
                    <span className='recharge_btn'>充值</span>
                </div>
                <div className='user_btn'>
                  <div className='user_center'>
                    <img src={users} alt="user_center"/>
                    <p>个人中心</p>
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div className='tourist'>
              <Icon style={{ fontSize: 18 }} type="user" />
              <span onClick={() => loginModal()}>登录</span>
              <span onClick={() => registerModal()}>注册</span>
            </div>
          )}
        </div>
      </div>
    </div>
    );
  }
}
export default connect(
  ({ user }) => ({
    user,
  }),
  dispatch => ({
    userInit() {
      dispatch(getInfo());
    },
    usetOut() {
      dispatch(userLoginOut());
    },
    loginModal() {
      dispatch(clickLogin());
    },
    registerModal() {
      dispatch(clickRegister());
    },
  }),
)(Header);
