/* eslint-disable no-continue */
import addDays from 'date-fns/add_days';
import format from 'date-fns/format';

/**
 * 根据域名判断当前环境状态
 */

export function getEnv() {
  return process.env;
}

/**
 * 获取代码运行场景值
 *
 */
export function getEnvironment() {
  const res = {
    system: '', // 系统
    sVersion: [], // 系统版本号
    app: '', // app 环境
    aVersion: [], // app 版本号
    channel: '', // 渠道
  };
  const ua = navigator.userAgent.toLowerCase();
  // 获取版本号
  if (/version/.test(navigator.userAgent)) {
    [, res.aVersion] = navigator.userAgent.match(/version\/(\S*)\s/);
  }
  if (/(iphone|ipad|ipod|ios)/i.test(ua)) {
    // ios 系统
    res.system = 'ios';
    res.sVersion = ua.match(/os (\d+)_(\d+)_?(\d+)?/);
  } else if (/(android)/i.test(ua)) {
    // android 系统
    res.system = 'android';
    res.sVersion = ua.match(/android (\d+)_(\d+)_?(\d+)?/);
  }
  if (/micromessenger/i.test(ua)) {
    res.app = 'wechat'; // 微信
    res.aVersion = '';
  } else if (/qq/i.test(ua)) {
    res.app = 'qq';
    res.aVersion = '';
  }
  return res;
}

/**
 * 判断参数具体类型，返回例如 String, Array
 * @param {Object} obj
 */
export function typeofObject(obj) {
  const class2type = {};
  'Boolean Number String Function Array Date RegExp Object Error'
    .split(' ')
    .forEach((e) => {
      class2type[`[object ${e}]`] = e.toLowerCase();
    });
  if (obj == null) {
    return String(obj);
  }
  // console.log(typeof obj);
  return typeof obj === 'object' || typeof obj === 'function'
    ? class2type[class2type.toString.call(obj)] || 'object'
    : typeof obj;
}

// 获取 url 里的 query
export function getQuery(name) {
  const reg = new RegExp(`(^|&)${name}=([^&]*)(&|$)`, 'i');
  const r = window.location.search.substr(1).match(reg);
  if (r !== null) {
    return unescape(r[2]);
  }
  return null;
}

/**
 * TODO: 函数节流
 * @param Function fn 延时调用函数
 * @param Number delay 延迟多长时间
 * @param Number atleast 至少多长时间触发一次
 * @return Function 延迟执行的方法
 */
export function throttle(fn, delay, atleast) {
  let timer = null;
  let previous = null;
  // eslint-disable-next-line
  return function() {
    const now = +new Date();
    if (!previous) previous = now;

    if (now - previous > atleast) {
      fn();
      // 重置上一次开始时间为本次结束时间
      previous = now;
    } else {
      clearTimeout(timer);
      timer = setTimeout(() => {
        fn();
      }, delay);
    }
  };
}

/**
 * 常用正则表达式校验
 * @param {*} type
 * @param {*} data
 */
export function verify(type, data) {
  const regObj = {
    phoneNum: '^1[3|4|5|6|7|8][0-9]\\d{4,8}$',
    // phoneNum: '^(0?(13|14|15|16|17|18|19)[0-9]{9}$)',
    // idCard: '(^\\d{15}$)|(^\\d{18}$)|(^\\d{17}(\\d|X|x)$)',
    // idCard: '^((\\d{18})|([0-9x]{18})|([0-9X]{18}))$',
    pwd: '[-_,!|~`()#$%^&*{}:;"L<>?]',
  };
  const reg = new RegExp(regObj[type], 'i');
  return reg.test(data);
}

/**
 * isEffective : 判断值是否有效
 * @param {*} target ：目标值
 * @param {Array} sample ：样本
 */
export function isEffective(target, sample) {
  if (sample.indexOf(target) > -1) {
    return true;
  }
  return false;
}

/**
 * 格式化显示未来时间
 * @param {String} formatter
 */
export function addDaysFormat(date, days, formatter) {
  return format(addDays(date, days), formatter);
}

/**
 * 获取 app user_agent 里的鉴权信息
 * @param {String} type 特定值
 */
export function getAuthentication(type) {
  const reg = new RegExp(`${type}/(\\w+)\\s?`, 'i');
  const res = navigator.userAgent.match(reg);
  if (res && res.length === 2) {
    return res[1];
  }
  return null;
}


export function getAuthenticationToken() {
  const token = localStorage.getItem('accessToken') || null;
  return token;
}

/**
 *  复制dom 里的文本信息
 * @export
 * @param {*} dom
 */
export function copyText(dom) {
  window.getSelection().removeAllRanges(); // 这段代码必须放在前面否则无效
  const text = dom; // 要复制文字的节点
  const range = document.createRange();
  // 选中需要复制的节点
  range.selectNode(text);
  // 执行选中元素
  window.getSelection().addRange(range);
  // 执行 copy 操作
  const successful = document.execCommand('copy');
  let msg = '';
  try {
    // Now that we've selected the anchor text, execute the copy command
    msg = successful ? '复制成功' : '复制失败';
    console.log(msg);
  } catch (err) {
    console.log(err);
  }
}

