// 直播流解密
export function xorEnc(str, key) {
  let crytxt = '';
  let k = '';
  const keylen = key.length;
  for (let i = 0; i < str.length; i += 1) {
    k = i % keylen;
    // eslint-disable-next-line no-bitwise
    crytxt += String.fromCharCode(str.charCodeAt(i) ^ key.charCodeAt(k));
  }
  return crytxt;
}
