import { INDEX_BANNER, INDEX_ROOM, INDEX_ATTENTION } from '../../constants/index/index';

const INITIAL_STATE = {
  banner: [],
  room: [],
  attention: [],
};

export default function counter(state = INITIAL_STATE, action) {
  switch (action.type) {
    case INDEX_BANNER:
      return {
        ...state,
        banner: action.data,
      };
    case INDEX_ROOM:
      return {
        ...state,
        room: action.data,
      };
    case INDEX_ATTENTION:
      return {
        ...state,
        attention: action.data,
      };
    default:
      return state;
  }
}
