import { message } from 'antd';
import { USER_INFO, USER_LOGIN_OUT } from '../../constants/user/index';
import { UserInfo, loginOut } from '../../../asset/js/api';

export const setInfo = (data, status) => ({
  type: USER_INFO,
  data,
  status,
});

export const notLogin = (data, status) => ({
  type: USER_LOGIN_OUT,
  data,
  status,
});

// 获取用户信息
export function getInfo() {
  return (dispatch) => {
    UserInfo().then((data) => {
      dispatch(setInfo(data, true));
    }).catch((err) => {
      console.log(err);
      dispatch(notLogin({}, false));
    });
  };
}
// 退出登录
export function userLoginOut() {
  return (dispatch) => {
    loginOut().then(() => {
      dispatch(notLogin({}, false));
    }).catch((err) => {
      message.error(err.msg);
    });
  };
}
